@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
:root {
  --dark-blue: #3A3053;
  --font: 'Poppins', sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: 'Poppins', sans-serif;
  font-family: var(--font);
  font-size: 18px;
}

h1 {
  font-size: 3rem;
}

h3 {
  font-size: 2.25rem;
}

h4 {
  font-size: 2rem;
}

h6 {
  font-size: 1rem;
}

/* UTILITY STYLES */

.hide {
  display: none;
}

.copied {
  background-color: #3A3053 !important;
  background-color: var(--dark-blue) !important;
}


.container {
  padding: 1rem;
}

.link {
  text-decoration: none;
}

.button1 {
  background-color: #2BD1D1;
  border-radius: 2em;
  border-style: none;
  padding: .25em 1em .4em 1em;
  color: white;
}

.button1:hover {
  background-color: #98E4E4;
  cursor: pointer;
}

@media (min-width:1000px) {
  .container {
    width: 1000px;
    margin-left: auto;
    margin-right: auto;
  }
}
.header {
    position: relative;
    display: flex;
    grid-column-gap: 2rem;
    -webkit-column-gap: 2rem;
            column-gap: 2rem;
    justify-content: space-between;
    align-items: center;
    padding-top: 1rem;
    padding-bottom: .5rem;
}

.header ul {
    list-style-type: none;
}

.header i {
    font-size: 1.75rem;
    color: #9D9BA6;
}

.navDesktopMenu {
    flex-grow: 1;

    display: none;
    justify-content: space-between;
}

.navDesktopMenu > ul {
    display: flex;
    align-items: center;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
}

.navDesktopMenu .link {
  color: #9D9BA6;
}

.navDesktopMenu .link:hover {
    color: black;
}

.navDesktopMenu .button1 {
    font-size: 1rem;
}

.navMobileMenu .link {
    font-size: 1rem;
    color: white;
    font-weight: 600;
}

.navMobileMenu .link:hover {
    color: #9D9BA6;
}

.navMobileMenu .button1 {
    font-size: 1rem;
    font-weight: 600;
    width: 100%;
    height: 100%;
}

.navMobileMenu {
    position: absolute;
    top: 3rem;
    background-color: var(--dark-blue);
    border-radius: 0.25rem;
    padding: 1rem;
    width: calc(100% - 2rem);
    text-align: center;
    color: white;
    z-index: 5;
}

.navMobileMenu > ul {    
    display: flex;
    flex-direction: column;
    grid-row-gap: .5em;
    row-gap: .5em;
    font-size: 1.5rem;
}

@media (min-width:900px) {
    .navDesktopMenu {
        display: flex;
    }

    .navMobileButton {
        display: none;
    }

    .navMobileMenu {
        display: none;
    }
}
.welcomeInfo {
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
}

.welcomeInfo h1 {
    color: #33303B;
}

.welcomeInfo div {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.welcomeInfo p {
    color: #9B9BA3;
}

.welcomeInfo .button1 {
    margin-top: 1rem;
    font-size: 1rem;
}

.welcomeInfo img {
    max-width: 480px;
    align-self: center;
    position: relative;
    left: 10vw;
}

@media (min-width:900px) {
    .welcomeInfo {
        flex-direction: row-reverse;
        grid-column-gap: 1rem;
        -webkit-column-gap: 1rem;
                column-gap: 1rem;
    }

    .welcomeInfo div {
        align-items: flex-start;        
        text-align: left;
    }

    .welcomeInfo img {
        position: static;
    }
}
.shortener {
    background-color: #F0F1F6;
}

.shortener > * {
    position: relative;
    z-index: 2;
}

.shortener::before {
    display: block;
    content: "";
    position: absolute;
    width: 100vw;
    height: 5rem;
    z-index: 1;
    background-color: white;
}

.shortenerForm {
    background-color: var(--dark-blue);
    background-image: url(/FEM_url-shortening-api-master/static/media/bg-shorten-mobile.41c34b33.svg);
    background-repeat: no-repeat;
    background-size: auto;
    background-position: right top;

    padding: 1rem;
    border-radius: .3rem;
    display: flex;
    flex-direction: column;
}

.shortenerItem {
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: .3rem;
    margin-top: 1rem;
}

.shortenerItem > hr {
    opacity: 0.1;
}

.shortenerItem > p {
    margin: 1rem;
    border-radius: .3rem;
}

.shortenerItem > p:last-of-type {
    color: #2BD1D1;
}

.shortenerItem > button {
    color: white;
    font-weight: bold;
    border: none;
    background-color: #2BD1D1;
    cursor: pointer;
    margin: 0 1rem 1rem 1rem;
}

.shortenerForm > input,
.shortenerForm > button {
    height: 3rem;
    font-size: 1rem;
    padding: 0 1rem;
    border-radius: .3rem;
}

.shortenerItem > button {
    height: 2.5em;
    font-size: .9rem;
    border-radius: .3rem;
}

.shortenerForm > button {
    color: white;
    font-weight: bold;
    border: none;
    background-color: #2BD1D1;
    cursor: pointer;

    margin-top: 1rem;
}

.shortenerForm > button:hover,
.shortenerItem > button:hover {
    background-color: #9BE3E2;
}

.shortenerLabel {
    color: #E6686B;
    font-style: italic;
    margin-top: .5rem;
}

@media (min-width:376px) {
    .shortenerForm {
        background-image: url(/FEM_url-shortening-api-master/static/media/bg-shorten-desktop.0d9742f4.svg);
        background-repeat: no-repeat;
        background-size: cover;
    }
}

@media (min-width:900px) {
    .shortenerForm {
        padding: 2rem;
        flex-direction: row;
        justify-content: center;
        grid-column-gap: 1rem;
        -webkit-column-gap: 1rem;
                column-gap: 1rem;
        position: relative;
    }

    .shortenerLabel {
        position: absolute;
        left: 10%;
        top: 5rem;
    }

    .shortenerInput {
        flex: 0 0 70%;
    }

    .shortenerSubmit {
        margin: 0 !important;
        flex: 0 0 20%;
    }

    .shortenerItem {
        flex-direction: row;
        justify-content: space-between;
        grid-column-gap: 1rem;
        -webkit-column-gap: 1rem;
                column-gap: 1rem;
        padding: 1rem;
        align-items: baseline;
    }

    .shortenerItem > * {
        margin: 0 !important;
        align-self: center;
    }

    .shortenerItem > p:first-of-type {
        flex: 1 1 40%;
    }

    .shortenerItem > p:last-of-type {
        flex: 0 1 40%;
        text-align: right;
    }

    .shortenerItem > button {
        flex: 0 1 10%;
    }

    .shortenerItem > hr {
        display: none;
    }
}
.features {
    background-color: #F0F1F6;
    text-align: center;
    padding-top: 2rem;
    padding-bottom: 2rem;
}

.featuresHeader {    
    margin-bottom: 4rem;
}

.featuresHeader > h3,
.featuresArticles > article > h4 {
    color: #33303B;
}

.featuresHeader > p,
.featuresArticles > article > p {
    color: #9B9BA3;
}

.featuresArticles {
    position: relative;
    display: flex;
    flex-direction: column;
    grid-row-gap: 4rem;
    row-gap: 4rem;
}

.featuresArticles > article {
    border-radius: .3rem;
    flex: 0 1 20%;
    padding: 3rem 1rem 1rem 1rem;
    position: relative;
    background-color: #fff;

    z-index: 5;
}

.featuresArticles::after {
    display: block;
    content: "";
    background-color: #2BD1D1;
    position: absolute;
    top: 4rem;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    width: .4rem;
    height: 75%;
    z-index: 4;
}

.featuresIcon {
    position: absolute;
    top: -2.25rem;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);

    background-color: var(--dark-blue);
    border-radius: 50%;
    width: 4.5rem;
    height: 4.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (min-width:900px) {
    .featuresArticles {
        flex-direction: row;
        justify-content: space-around;
    }

    .featuresArticles > article {
        flex: 0 0 30%;
    }

    .featuresArticles {
        padding: 2rem 0;
    }

    .featuresArticles > article:first-of-type {
        top: -2rem;
    }
    
    .featuresArticles > article:last-of-type {
        top: 2rem;
    }

    .featuresArticles::after {
        top: 50%;
        left: 6rem;
        height: .5rem;
        width: 75%;
        -webkit-transform: translate(0, -50%);
                transform: translate(0, -50%);
    }
}
.boost {
    padding: 5rem 0;
    background-color: var(--dark-blue);
    background-image: url(/FEM_url-shortening-api-master/static/media/bg-boost-mobile.4b04bd89.svg);
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
}

.boost h3 {
    color: white;
    margin-bottom: 1rem;
}

.boost .button1 {
    font-size: 1.25rem;
}

@media (min-width:376px) {
    .boost {
        background-image: url(/FEM_url-shortening-api-master/static/media/bg-boost-desktop.772154c9.svg);
    }
}
.footer {
    background-color: #232027;
}


.footer .container {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding-top: 2rem;
    padding-bottom: 2rem;
}

.footer .container > h4 {
    color: white;
    flex: 1 0 20%;
}

.footer .container > div {
    flex: 1 0 15%;
}

.footer .container > * {
    margin: .5rem 0;
}

.footer .container h6 {
    margin: 1rem 0;
    color: white;
}

.footer .container ul {
    list-style-type: none;
}

.footer .container li {
    margin: .75rem 0;
}

.footer .container a {
    color: #CFCCD3;
    text-decoration: none;
}

.socialLinks {
    display: flex;
    justify-content: center;
    grid-column-gap: 1.25rem;
    -webkit-column-gap: 1.25rem;
            column-gap: 1.25rem;
}

.socialLinks a:hover {
    opacity: 0.8;
}

@media (min-width:900px) {
    .footer .container {
        flex-direction: row;
    }
}
